<template>
  <div class="take">
    <v-container>
      <v-card>
        <v-card-text>
          <h2>Create Session</h2>
          <v-select @change="getlecture()" :items="courses" v-model="course" label="Course"></v-select>
          <v-select @change="getlecture()" :items="years" v-model="year" label="Year"></v-select>
          <v-select :items="lecture" label="Select Lecture" v-model="currentLecture"></v-select>
          <v-layout v-if="this.currentLecture == 'Custom'">
            <v-flex xs6 sm6 md6 pr-2>
              <v-dialog
                ref="startDialog"
                v-model="startTimeModal"
                :return-value.sync="startTime"
                persistent
                lazy
                full-width
                width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="startTime"
                    label="Start Time "
                    readonly
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-time-picker v-if="startTimeModal" v-model="startTime" full-width>
                  <v-spacer></v-spacer>
                  <v-btn flat color="primary" @click="startTimeModal = false">Cancel</v-btn>
                  <v-btn flat color="primary" @click="$refs.startDialog.save(startTime)">OK</v-btn>
                </v-time-picker>
              </v-dialog>
            </v-flex>
            <v-flex xs6 sm6 md6 pl-2>
              <v-dialog
                ref="endDialog"
                v-model="endTimeModal"
                :return-value.sync="endTime"
                persistent
                lazy
                full-width
                width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="endTime"
                    label="End Time"
                    readonly
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-time-picker v-if="endTimeModal" v-model="endTime" full-width>
                  <v-spacer></v-spacer>
                  <v-btn flat color="primary" @click="endTimeModal = false">Cancel</v-btn>
                  <v-btn flat color="primary" @click="$refs.endDialog.save(endTime)">OK</v-btn>
                </v-time-picker>
              </v-dialog>
            </v-flex>
          </v-layout>
          <v-layout v-if="this.currentLecture == 'Custom'">
            <v-flex xs4 sm4 md4 pr-2>
              <v-select :items="['RY','MA']" label="Faculty" v-model="faculty"></v-select>
            </v-flex>
            <v-flex xs4 sm4 md4 pl-2 pr-2>
              <v-select :items="['NGT','IOT']" label="Subject" v-model="subject"></v-select>
            </v-flex>
            <v-flex xs4 sm4 md4 pl-2>
              <v-select :items="['IT LAB','505']" label="Classroom" v-model="classroom"></v-select>
            </v-flex>
          </v-layout>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn @click="starttaking()">Start</v-btn>
          </v-card-actions>
        </v-card-text>
      </v-card>
    </v-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      startTime:"",
      startTimeModal:false,
      endTime:"",
      endTimeModal:false,
      courses: ["BSCIT", "BSCCS", "BMM", "BFM", "BA", "BCOM", "BBI", "BMS"],
      course: "BSCIT",
      years: ["FY", "SY", "TY"],
      year: "FY",
      lecture: ['Custom'],
      currentLecture: "Custom",
      sessionid: 0,
      faculty: "",
      subject: "",
      noOfLect: 0,
      classroom: ""
    };
  },
  computed: {
    classes() {
      return this.year + this.course;
    }
  },
  methods: {
    starttaking() {
      var today = new Date();
      this.sessionid = 0;
      for (var i = 0; i < this.classes.length; i++) {
        // console.log(this.classes.charCodeAt(i));
        this.sessionid = this.sessionid + this.classes.charCodeAt(i);
        console.log(this.sessionid);
      }
      this.sessionid =
        this.sessionid *
        today.getDate() *
        today.getMonth() *
        today.getFullYear() *
        today.getHours() *
        today.getMinutes();
      if (this.sessionid >= 9999) {
        this.sessionid = this.sessionid % 10000;
      }
      if (this.sessionid <= 999) {
        this.sessionid = this.sessionid + today.getFullYear();
      }
      alert(this.sessionid);
      firebase
        .database()
        .ref("sessions/" + this.sessionid + "/lecture/")
        .set({
          status: "started",
          faculty: this.faculty,
          subject: this.subject,
          noOfLect: this.noOfLect,
          classroom: this.classroom,
          timeSlot: this.currentLecture,
          timestamp: Date.now()
        });
      this.$router.push("/Session/" + this.sessionid);
    },
    getlecture() {
      var days = [
        "sunday",
        "monday",
        "tuesday",
        "wednesday",
        "thursday",
        "friday",
        "saturday"
      ];
      var today = new Date();
      this.lecture = [];
      firebase
        .database()
        .ref("lectures/" + this.classes + "/" + days[today.getDay()] + "/")
        .once("value", snapshot => {
          console.log(snapshot.val());
          snapshot.forEach(lecture => {
            var lect = lecture.val();
            var time = today.getHours() * 60 + today.getMinutes();
            var starttime = lect.starthr * 60 + lect.startmin;
            var endtime = lect.endhr * 60 + lect.endmin;
            if (time >= starttime && time <= endtime) {
              this.currentLecture =
                lect.subject + "(" + lect.faculty + ")" + " - " + lect.starthr + ":" + lect.startmin + " to " + lect.endhr + ":" + lect.endmin;
            }
            this.lecture.push(
              lect.subject + "(" + lect.faculty + ")" + " - " + ('0' + lect.starthr).slice(-2) + ":" + ('0' + lect.startmin).slice(-2) + " to " + ('0' + lect.endhr).slice(-2) + ":" + ('0' + lect.endmin).slice(-2)
            );
          });
          
      this.lecture.push('Custom')
        });
    }
  }
};
</script>

<style>
</style>


