<template>
    <v-container>
        <h1>{{this.$route.params.sessionid}}</h1>
        <ul>
            <li v-for="session in sessions">{{session}}</li>
        </ul>
    </v-container>
</template>

<script>
    export default {
        data(){
            return{
                sessions:[]
            }
        },
        mounted(){
        }
    }
</script>

